.new-chat{
    width:35%;
    max-width:415px;
    position:fixed;
    left:0;
    top:0;
    bottom:0;
    background-color:#fff;
    display:flex;
    flex-direction:column;
    border-right:1px solid #ddd;
    transition:all ease 0.5s;
}

.new-chat-head{
    display:flex;
    background-color:#00bfa5;
    align-items:center;
    padding:60px 15px 15px 15px;
}

.new-chat-back-button{
    width:40px;
    height:40px;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor:pointer;
}

.new-chat-head-title{
    color:#fff;
    font-size:19px;
    height:40px;
    line-height:40px;
    flex:1;
    font-weight:bold;
    margin-left:20px;
}

.new-chat-list{
    flex:1;
    overflow-y:auto;
}

.new-chat-list::-webkit-scrollbar{
	width:6px;
	height:6px;
}

.new-chat-list::-webkit-scrollbar-thumb{
	background-color:rgba(0,0,0,0.2);
}

.new-chat-item{
    display:flex;
    align-items:center;
    padding:15px;
    cursor:pointer;
}

.new-chat-item:hover{
    background-color:#f5f5f5;
}

.new-chat-item-avatar{
    width:50px;
    height:50px;
    border-radius:50%;
    margin-right:15px;
}

.new-chat-item-name{
    font-size:17px;
    color:#000;
}